<template>
  <div class="page001 uk-text-center">
    <h1>申报历史数据查询</h1>
    <div class="uk-flex uk-flex-center uk-margin-large-top"
         style="background: #e3eceb; width: 450px; border:1px solid #666; padding: 5px; margin: 0 auto">
      <form action="" class="uk-form" style="font-size: 16px">
        <b style="">申报月份:　</b>
        <span><input v-model="date" type="text">　(例如201804)</span>
      </form>
    </div>
    <div class="uk-margin-large-top">
      <button @click="query" class="uk-button uk-button-primary"><i class="uk-icon uk-icon-search"></i> 查询</button>
    </div>
  </div>
</template>

<script>
  import mymixin from '../../mixins/common'

  export default {
    mixins: [mymixin],
    data() {
      return {
        date: ''
      }
    },
    methods: {
      query() {
        if (this.date === '') {
          this.$Message.error('请输入查询日期')
          this.date = ''
          return
        } else if (this.date.length !== 6) {
          this.$Message.error('查询日期不合法')
          this.date = ''
          return
        }
        this.eg('本系统为教学演示系统，无法查询申报历史数据')
      }
    }
  }
</script>





